  color: hsl(23, 95%, 52%);
}

.balmanjariLogo {  
  display: flex;
  align-items: center;
  flex-direction: column;
  color: hsl(23, 95%, 52%);
}

.balmanjariLogo img {
  height: 50vmin;
  width: auto;
  margin: 3vmin 0vmin 0vmin 0vmin;
