}

/* Animation banner begin */

.banner{
  text-align: center;
  text-transform: uppercase;
}

.banner .heading {
  /* display: inline-block; */
  display: flex;  
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  /* color: hsl(23, 95%, 52%); */
  color: hsl(48, 93%, 53%);
  font-family: 'Righteous', serif;
  font-size: 7vmin; 
  text-shadow: .03em .03em 0 rgb(224, 160, 100);
  }
  .banner .heading:after {
    content: attr(data-shadow);
    /* display: flex; */
    position: absolute;
    top   : 0.8vmin;
    left  : 1.5vmin;
    bottom: 0;
    right : 0;
    z-index: -1;
    text-shadow: none;
    background-image:
      linear-gradient(
        45deg,
        transparent 45%,
        rgb(236, 87, 18) 45%,
        rgb(226, 100, 42) 55%,
        transparent 0
        );
    background-size: .05em .05em;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  
    animation: shad-anim 15s linear infinite;
    }

@keyframes shad-anim {
  0% {background-position: 0 0}
  0% {background-position: 50% -50%}
  }

/* Animation banner end */

.App .homeContent{
  margin: 2vmin 20vmin 2vmin 20vmin;
  z-index: 3;
  font-size: 4vmin;
  text-align: justify;
  text-justify: inter-word;
}

.topnav {
  overflow: hidden;
  /* background-color: hsl(23, 95%, 52%); */
  background-color: hsl(32, 97%, 46%);
}

.topnav a {
  float: left;
  display: block;
  /* color: #f2f2f2; */
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.topnav a.active {
  background-color: hsl(48, 93%, 53%);
  color: black;
}

.topnav .icon {
  display: none;
  font-size: 6vmin;
}

@media screen and (max-width: 600px) {
  .topnav a:not(:first-child) {display: none;}
  .topnav span.icon {
    float: right;
    display: block;
    position: absolute;
    right: 2vmin;
    top: 2vmin;
  }
}

@media screen and (max-width: 600px) {
  .topnav.responsive {position: relative;}
  .topnav.responsive .icon {
    position: absolute;
    right: 2vmin;
    top: 2vmin;
  }
  .topnav.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
}

@media only screen and (max-width: 500px){
  .App .homeContent{
    margin: 2vmin 10vmin 2vmin 10vmin;
    z-index: 3;
    font-size: 6vmin;
    text-align: justify;
    text-justify: inter-word;
  }
}
